let hljs;
let marked;
require.ensure([], function() { hljs = require('highlight.js')}, 'highlight');
require.ensure([], function() { marked = require('marked')}, 'marked');

export default {
    name: "TextPlayer",
    data() {
        return {
            text: '',
            loading: true
        }
    },
    props: {
        file: Object
    },
    methods: {
        getFileSuffix(name) {
            return name.substr(name.lastIndexOf('.') + 1);
        },
        init() {
            let file = this.file;

            this.$http.get('api/content', {params: {url: file.url}}).then((response) => {
                this.loading = false;
                this.text = response.data.data;
            }).catch(() => {
                this.$http.get(file.url).then((response) => {
                    this.loading = false;
                    this.text = response.data;
                })
            });


        }
    },
    computed: {
        markdownHtml() {
            // url 新窗口打开.
            let renderer = new marked.Renderer();
            renderer.link = function() {
                let link = marked.Renderer.prototype.link.apply(this, arguments);
                return link.replace("<a","<a target='_blank'");
            };
            marked.setOptions({
                renderer: renderer
            });

            return marked(this.text, {
                highlight: function(code) {
                    return hljs.highlightAuto(code).value;
                }
            });
        },
        highlightText() {
            if (this.loading) {
                return '';
            } else if (this.file.size > 10240) {
                this.$message('文件内容过多, 取消高亮显示');
                return this.text;
            } else {
                let result = '';
                try {
                   result = hljs.highlightAuto(this.text).value;
                } catch (e) {
                    result = this.text;
                }
                return result;
            }
        },
        fileType() {
            return this.getFileSuffix(this.file.name) === 'md' ? 'markdown' : 'text';
        }
    }
}
